import { Alert, Button, Divider, Typography } from "antd";
import { ButtonLink } from "../../Data/Components";
import Links from "../../Data/Links";

const MenuItem = {
    label: <Typography.Text type="danger"></Typography.Text>,
    key: "/Introduction/FAQ",
    page: () => <div>
        <Divider children=""></Divider>
        <Alert
            showIcon
            type="success"
            message="Issue"
            description={<ButtonLink
                style={{ maxWidth: "500px" }}
                href="https://gitee.com/bdffzi/Page.OpenCOM3D2/issues?state=closed">
                
            </ButtonLink>}
        />
        <br></br>
        <Alert
            showIcon
            type="success"
            message="WIKIWIKIWIKI"
            description={<ButtonLink
                style={{ maxWidth: "500px" }}
                href={Links.get("COM3D2SeesaaWiki")}>
                COM3D2 WIKI
            </ButtonLink>}
        />
        <br></br>
        <Alert
            showIcon
            type="success"
            message="CMI"
            description={
                <Button
                    ghost
                    type="primary"
                    style={{ maxWidth: "500px", pointerEvents: "none" }}>
                    CMI Documentation
                </Button>}
        />
        <Divider children=""></Divider>
        <Alert
            showIcon
            type="warning"
            message={
                <div>
                    
                </div>
            }
            description={

                <div>
                    <Typography.Paragraph>
                        
                        <div></div>
                        IssueIssue
                    </Typography.Paragraph>

                    <ButtonLink
                        style={{ maxWidth: "500px" }}
                        href={"https://gitee.com/bdffzi/Page.OpenCOM3D2/issues"}>
                        Issue
                    </ButtonLink>
                </div>
            }
        />
    </div>
}

export default MenuItem;
